<template>
    <v-container>
        <v-row justify="center">
            <v-col cols="12" md="5">
                <h2>Login</h2>
                <h3>User: test@test.cl pass: 123456</h3>
                <v-form ref="form" v-model="valid">
                    <v-text-field 
                        v-model="email" 
                        :rules="emailRules" 
                        label="E-mail" 
                        required>
                    </v-text-field>
                    <v-text-field 
                        v-model="pass" 
                        :rules="passRules" 
                        label="Contraseña" 
                        type="password"
                        required>
                    </v-text-field>

                    

                    <v-btn :disabled="!valid" color="success" class="mr-4" @click="login">
                        Ingresar
                    </v-btn>

                    <v-btn color="error" class="mr-4" @click="reset">
                       Limpiar formulario
                    </v-btn>

                   
                </v-form>
            </v-col>
        </v-row>
    </v-container>
</template>

<script>
import {getAuth, signInWithEmailAndPassword } from 'firebase/auth';
export default {
    name: 'login-view',
    // props: {},
    data: function () {
        return {
            valid:true,
            email: '',
            emailRules: [
                v => !!v || 'E-mail is required',
                v => /.+@.+\..+/.test(v) || 'E-mail must be valid',
            ],
            pass:'',
            passRules: [
                v => !!v || 'Contraseña requerida',
                v => (v && v.length >= 6) || 'Contraseña debe contener al menos 6 caracteres',
            ],
            
        }
    },
    // computed: {},
    methods: {
        validate () {
        this.$refs.form.validate()
      },
      reset () {
        this.$refs.form.reset()
      },
      login(){
        this.validate();
        if(!this.valid) return;

        const auth = getAuth();
        signInWithEmailAndPassword(auth, this.email, this.pass)
        .then((userCredential)=>{
            const user = userCredential.user;
            console.log(user);
            this.$router.push('/');
        })
        .catch(error=>{
            const errorMsg = error.message;
            console.log(errorMsg);
        })
      }
    }
    // watch: {},
    // components: {},
    // mixins: [],
    // filters: {},
    // -- Lifecycle Methods
    // -- End Lifecycle Methods
}
</script>

<style scoped></style>